<template>
  <swiper :options="swiperOption">
    <swiper-slide>
      <Tile></Tile>
    </swiper-slide>
    <swiper-slide>
      <Tile></Tile>
    </swiper-slide>
    <swiper-slide>
      <Tile></Tile>
    </swiper-slide>

    <swiper-slide>
      <Tile></Tile>
    </swiper-slide>
    <swiper-slide>
      <Tile></Tile>
    </swiper-slide>
    <swiper-slide>
      <Tile></Tile>
    </swiper-slide>

    <div class="swiper-button-prev" slot="button-prev"></div>
    <div class="swiper-button-next" slot="button-next"></div>
  </swiper>
</template>

<script>
import Tile from "./Tile.vue";
import { Swiper, SwiperSlide, directive } from "vue-awesome-swiper";
import "swiper/css/swiper.css";

export default {
  name: "tile-slider",
  components: {
    Tile,
    Swiper,
    SwiperSlide
  },
  data() {
    return {
      swiperOption: {
          effect: 'coverflow',
          slidesPerView: '3',
          slidesPerGroup: '3',
          navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev'
          },
          coverflowEffect: {
            rotate: 0,
            slideShadows: false,
            stretch: -30,
            depth: 50
          }
        }
    };
  },
  directives: {
    swiper: directive
  }
};
</script>

